.similarAvailableHomes {
  margin: 7rem 0;
}
.similarAvailableHomesHeading {
    position: relative;
    height: 5rem;
    line-height: 5rem;
    color: #fff;
    text-align: right;
    width: 40%;
    padding-right: 5rem;
    font-family: Gotham-Thin;
    font-size: 2em;
    font-style: italic;
}
.similarAvailableHomesHeading img{
  position: absolute;
  top: 0;
  left: 0;
  z-index: -1;
  height: 100%;
  width: 100%;
}
.homesContainer {
  width:90%;
  margin: 5% auto;
  position: relative;
  overflow: hidden;
}
.homesContainerInner{
  width:100%;
  overflow: hidden;
}
.homesContainer .homeItemContainer {

}



