
.elevationImageContainer {
  width: 100%;
}
.elevationImageContainer img{
  width: 100%;
  display: block;
}
.infoContainer {
  width: 100%;
  position: relative;
}
.town-backgroundColor {
  background: #FBCD5A;
}
.semi-backgroundColor {
  background: #BEB8A9;
}

.elevationToggleButtonContainer {
  position: absolute;
  top: -2.5rem;
  /* move the element half way across the screen */
  left: 50%;
  /* allow the width to be calculated dynamically */
  width: auto;
  /* then move the element back again using a transform */
  -webkit-transform: translateX(-50%);
      -ms-transform: translateX(-50%);
          transform: translateX(-50%);
}

.elevationToggleButton {
  cursor: pointer;
  float: left;
  line-height: 1.5rem;
  font-size: 1.5rem;
  padding: .5rem 2rem;
  margin: 0 .3rem;
  border-top-left-radius: .3rem;
  border-top-right-radius: .3rem;
  font-family: GothamMedium;
}
.elevationToggleButtonActive {
  color: #fff;
}
.elevationToggleButtonInactive {
  color: #008dd3;
  background: #fff;
}


nav {
  width: 100%;


}
.clr{
    clear:both;
    font-size:0;
}

nav ul {
  list-style: none;
  padding: 0;
  margin: 0;
  height: 100%;
  width: 100%;
  text-align: center;
}



nav ul li {
  color: white;
  text-decoration: none;
  display: block;
  width: 100%;
  text-align: center;
  height: 100%;
  line-height: 65px;
  font-size: 1.2em;
  &:hover {
    background: #160220;
  }
}nav ul li {
  display: inline-block;
  margin-right: -4px;
  width: auto;
  height: 100%;
  padding: 0 20px;
}
li.productDetailsIcon.backbutton {
  width: 5%;
  float: left;
}

.productDetailsIcon {
    display: inline-block;
    -ms-flex-align: center;
        align-items: center;
    -ms-flex-pack: center;
        justify-content: center;
}

.signupBtn {
  width: 100%;
  cursor: pointer;
}

/*BOOTSTRAP*/
.thumbnail a>img, .thumbnail>img {
    border-radius: 8px;
    -webkit-box-shadow: 10px 10px 13px -5px rgba(161,156,161,1);
    box-shadow: 10px 10px 13px -5px rgba(161,156,161,1);
}
.homesDesigns_Heading{
  margin-top: 5%;
}
.floorplansContainer {
  margin-top: 5rem;
  position: relative;
}
.spacerForFloats {
  clear:both;
}
.optButton {
  padding: 1rem;
  background: #158DCD;
  color: #FFF;
  border-radius: .5rem;
  position: absolute;
  width: 10rem;
  top: 79%;
  left: 35%;
  cursor: pointer;
}
.calloutIcon {
  position: absolute;
  width: 7%;
  cursor: pointer;
}
.calloutIcon img {
  width: 100%;
  display: block;
}
.floorplansContainer .ground {
  margin-left: 15%;
}
.floorplansContainer .basement {
  margin-left: 10%;
}
.floorplansContainer .floorplan {
  width: 30%;
  float: left;
  position: relative;
}
.floorplansContainer .floorplan img{
  width: 100%;
}

.productName {
  text-transform: uppercase;
  font-size: 2rem;
  font-family: GothamLight;
  font-weight: 800;
}
.productStyle{
  font-size: 2rem;
  font-family: GothamLight;
  text-transform: uppercase;
}
.productPrice{
  font-size: 2rem;
  font-family: GothamLight;
}
.productSquareFoot{
  font-size: 2rem;
  font-family: GothamLight;
}
.productBedroom {
  font-size: 2rem;
  font-family: GothamLight;
}
.productBathroom {
  font-size: 2rem;
  font-family: GothamLight;
}

.mobileSignupBtn {
  text-align: center;
  display: none;
}

.floorplanNavigator {
  display: none;
}
.floorplansContainerInner {
}

.calloutContainer {
  position: absolute;
  color: #FFF;
  width: 70%;
  display: none;
  -webkit-transform:translate(-45%,-100%);
      -ms-transform:translate(-45%,-100%);
          transform:translate(-45%,-100%);
}
.calloutImage {
  width: 98%;
  margin: 0 auto;
}
.calloutImage img {
  width: 100%;
  display: block;
}
.calloutText {
  width: 90%;
  margin: 0 5%;
  padding: 1rem 0;
  text-align: center;
  font-family: GothamLight;
  font-size: 1.5rem;
}
.calloutContainerInner {
  background: #158DCD;
}
.calloutBottomSliver {
  width: 100%;
  margin-top: -5px;
}
.calloutBottomSliver img {
  width: 100%;
  display: block;
}
@media screen and (max-width: 1296px) and (min-width: 1025px) {
    li.productDetailsIcon.productSignupBtn {
        width: 20%;
    }

        .productName {
      text-transform: uppercase;
      font-size: 1.4rem;
      font-family: GothamLight;
      font-weight: 800;
    }
    .productStyle{
      font-size: 1.4rem;
      font-family: GothamLight;
      text-transform: uppercase;
    }
    .productPrice{
      font-size: 1.4rem;
      font-family: GothamLight;
    }
    .productSquareFoot{
      font-size: 1.4rem;
      font-family: GothamLight;
    }
    .productBedroom {
      font-size: 1.4rem;
      font-family: GothamLight;
    }
    .productBathroom {
      font-size: 1.4rem;
      font-family: GothamLight;
    }
}

@media(max-width:2000px){
  .floorplanNavigator {
    display: none;
  }
}

@media(max-width:1024px){
  .mobileSignupBtn {
    text-align: center;
    display: block;
    margin:auto;
    margin-top: 2%;

  }
  .productSignupBtn {
    display:none;
  }
}
@media(max-width:650px){
  /*mobile for floorplans*/
  .renderedElevation {
    margin-bottom: 5rem;
  }
  .floorplansContainerInner {
    width: 200%;
    position: relative;
    -webkit-transition: width 2s; /* Safari */
   -o-transition: width 2s;
   transition: width 2s;
  }
  .floorplansContainer .floorplan {
    width: 38%;
    margin-left: 6%;
  }
  .floorplanNavigator {
    position: absolute;
    font-size: 3em;
    color: #158dcd;
    top: 45%;
    cursor: pointer;
    width: 6%;
  }
  .floorplanNavigator img {
    display: block;
    width: 100%;
  }
  .floorplanNavigatorLeft {
    left: 2%;
    display: none;
  }
  .floorplanNavigatorRight {
    left: 91%;
    display: block;
  }
  .floorplanIndicator {
    background: #158dcd;
    width: 2rem;
    height: 2rem;
    border-radius: 1000px;
    display: inline-block;
    margin-top: 2rem;
    border: 2px solid #158dcd;
  }
  .floorplanIndicatorLeft {
      left: 30%;
  }
  .floorplanIndicatorRight {
      left: 35%;
      margin-left: 2rem;
  }
  .renderedElevation {
    text-align: center;
  }
  .floorplanIndicatorActive {
    background: none;
  }
  .floorplanIndicatorActive  .floorplanIndicatorInner  {
    background: #158dcd;
    width: 1rem;
    height: 1rem;
    margin-top: .25rem;
    margin-left: .25rem;
    border-radius: 1000px;
  }
}
@media(max-width:768px){
    .productName {
      text-transform: uppercase;
      font-size: 1.5rem;
      font-family: GothamLight;
      font-weight: 800;
    }
    .productStyle{
      font-size: 1.5rem;
      font-family: GothamLight;
      text-transform: uppercase;
    }
    .productPrice{
      font-size: 1.5rem;
      font-family: GothamLight;
    }
    .productSquareFoot{
      font-size: 1.5rem;
      font-family: GothamLight;
    }
    .productBedroom {
      font-size: 1.5rem;
      font-family: GothamLight;
    }
    .productBathroom {
      font-size: 1.5rem;
      font-family: GothamLight;
    }

  }


  @media(max-width:414px){
    nav ul li {
      padding:0 15px;
      line-height: 30px;
    }
    .productIcon {
      width: 50%;
    }
    li.productDetailsIcon.backbutton {
      display: none;
    }

    .homesContainerInner.col-xs-12 {
    padding-left: 0;
    padding-right: 0;
    }
    .homesContainer {
      width: 85%;
    }
    .elevationToggleButton {
      width: 44%;
      margin-left: 4%;
      text-align: center;
    }
    .elevationToggleButtonContainer {
      left: 10%;
      width: 80%;
      -ms-transform: none;
          transform: none;
      -webkit-transform: none;
    }
    /*.floorplansContainer .floorplan {
      width: 53%;
      float: none;
    }
    .floorplansContainer .ground {
      margin: 0 auto;
    }
    .floorplansContainer .basement {
      margin:0 auto;
    }*/
  }



