.container {
	width: 81%;
}
.bonusHeader {
	font-size:2em;
  margin-bottom: 10px;
  margin-top: 0;
  font-weight: 600;
}

p {
  line-height: 2.5rem;

}
.upgradesSecondary {
  line-height: 1.5;
  font-size: 1.5em;
  font-weight: 300;
}

.btnflt3:hover {
color: #ffffff;
background: #e7e7e7;
text-shadow: 0 1px 0px rgba(255, 255, 255, .25);
border: 1px solid #08c;
-o-transition:.5s;
-ms-transition:.5s;
-moz-transition:.5s;
-webkit-transition:.5s;
text-decoration:none;
}
.btnflt3 {
display: inline-block;
padding: 5px 30px;
margin-bottom: 0;
font-size: 18px;
line-height: 20px;
color: #008dd3;
text-align: center;
text-shadow: 0 1px 0px rgba(255, 255, 255, .125);
vertical-align: middle;
cursor: pointer;
background-color: #ffffff;
border: 1px solid #e7e7e7;
border-radius: 4px;
}

.innerKitchenContainer {
  display: -ms-flexbox;
  display: flex;
}




