@media (min-width: 1441px) {
	.container.kitchenContainer {
		margin-right: 10%;
	}
}

@media(max-width:1440px) {
  .container.kitchenContainer {
    margin-right: 10%;
  }
}

/*@media(max-width:1248px) {
    .container.kitchenContainer {
