  }
}*/

@media(max-width:1248px) {
  .container.kitchenContainer {
    margin-right: 11%;
  }
  .bonusHeader {
    font-size: 2.2em;
  }
  .upgradesSecondary {
    font-size: 1.2em;
    line-height: 1;
    font-weight: 300;
  }
}


@media(max-width:1024px) {
  .bonusHeader {
    font-size: 2em
  }
}

@media(max-width:991px) {
  .text_box {
    margin-left: 30%;
  }
  .map_box {
    margin-left: 30%;
  }
  .upgradesSecondary {
    font-size: 1em;
  }
}
@media(max-width:1024px) {
  .bonusHeader {
    font-size: 1.5em
  }
}

@media(max-width:768px) {
  .container.kitchenContainer {
    margin-right: 0px;
    width: 100% !important;
    padding-right: 0;
    padding-left: 0;
  }
  .innerKitchenContainer {
    display: grid;
  }
  .kitchenRendering-img {
    width: 100%;
  }
  .col-md-6.col-sm-12.kitchen_container {
    padding-left: 0px;
  }
  .bonusHeader {
    font-size: 3em;
    padding-top: 0.7em;
  }
  .btnflt3 {
    margin-bottom: 2em;
  }
  .upgradesSecondary {
    font-size: 1.5em;
  }
}

@media(max-width:414px) {
  .bonusHeader {
    font-size: 2em;
    padding-top: 0.5em;
  }
  .upgradesSecondary {
    font-size: 1.3em;
  }
}

@media(max-width:320px) {
  .bonusHeader {
    font-size: 2em;
    padding-top: 0.5em;
  }
  .upgradesSecondary {
    font-size: 1.1em;
    padding-top: 5px;
  }
}



