.adult-condo h1 {
  font-family: GothamLight;
  color: #008dd3;
  width: 58%;
}
.condo-text {
  position: relative;
  width: 60%;
  font-family: Gotham-Book;

}

.condo-text p {
  width: 34rem;
  top: .9rem;
  font-family: GothamBook;
  line-height: 1.5em;
  font-size: 1.2em;
}

.text_box {
  margin: 0 15rem;
  width: 48rem;
}

.mapRow{
  margin: 3rem 0 !important;
}
.adult-condo > img{
  max-width: 100%;
}

.map_box {
  margin-top: auto;
}

#mapper {
  width: 80%;
  border: .5px solid gray;
}
.breezeLogo{
  display: none;
}
.building-text{
  font-family: GothamBook;
  font-size: 1.5rem;
}

.condo-text.besideMapLarge {
    padding-top: 25%;
    width: 100%;
}

.besideMapSmall {
  font-family: GothamBold;
}

.besideMapSmallMobile {
  font-family: GothamBold;
}

.saleCentreAddressMobile {
  text-align: center;
  display: none;
}

hr.addressHr {
    display: block;
    height: 1px;
    border: 0;
    border-top: 2px solid black;
    padding: 0;
    width: 70%;
    text-align: left;
    padding-left:0;
    margin-left: 0;
    margin-top: 0;
}

@media(min-width:1500px) {
hr.addressHr {
    width: 54%;
  }
  .condo-text.besideMapLarge {
    padding-top: 24%;
}
}

@media(max-width:1500px) {
.condo-text.besideMapLarge {
    padding-top: 10%;
  }
  hr.addressHr {
    width: 63%;
  }
}

@media(max-width:1499px) and (min-width:1281px){
            .contactMapContainer {
      padding-top: 3%;
    }

}


@media(max-width:1280px) {
    .adultCondoHeader {
      margin:0px 8rem;
    }
        .contactMapContainer {
      padding-top: 5%;
    }
}

@media(max-width:1219px) {
    .map_style {
      padding-top: 3%;
    }

}

@media(max-width:1200px) and (min-width:991px){
.condo-text.besideMapLarge {
    padding-top: 9%;
}
  .contactMapContainer {
      padding-top: 4%;
    }
.text_box {
  margin: 0 9rem;
  width: 48rem;
  }
    hr.addressHr {
    width: 73%;
  }
}

@media(max-width:1024){
    .condo-text.besideMapLarge {
    padding-top: 0%;
}
.condo-text.besideMapLarge {
    padding-top: 10%;
}

  }
@media(max-width:991px){
  .text_box{
    top: 0;
    left: 0;
    margin: 0 !important;
    width: 100% !important;
  }
  .map_box{
    text-align: center;
    margin-left: 0 !important;
  }
  .adult-condo{
    margin: auto;
    width: 80%;
  }
  .adult-condo > img{
    content:('../images/mapText.jpg');
  }
  .condo-text{
    width: 80%;
    margin: 0 auto;
  }
}
@media(max-width:991px){
  .breezeLogo{
    display: block;
    max-width: 70%;
    margin: auto;
    margin-top: 3rem;
  }
  #mapper{
    height: auto;
  }
  .FormContainer{
    text-align: center;
  }
  .FormDisclaimerContainer{
    text-align: left;
  }
  .FormButtonText{
    width: 75%;
    padding: 1rem;
  }
  .text_box {
    margin:0;
  }
  .condo-text p {
    width: 100%;
    margin: 0;
    top: .9rem;
    font-family: GothamBook;
  }
  .saleCentreAddressMobile {
    display: block;
  }
  .besideMapLarge {
    display: none;
  }
}

@media(min-width:1350px){
  .text_box {
    margin: 0 19rem;
  }
}

@media(max-width:991px) and (min-width:600px){
  .breezeLogo{
    max-width: 45%;
  }
}

@media(max-width:991px) and (min-width:768px){
      .map_style {
        padding-top: 0;
      }
      #mapper {
        width: 84%;
      }
      .besideMapLargeMobile {
        width: 100%;

      }
      .building-text.besideMapSmallMobile{
              font-size: 1.2rem;
            }
.building-text.besideMapAddressMobile{
              font-size: 1.2rem;
            }

            .adult-condo h1 {
              width: 94%;
            }
/*      .building-text.besideMapSmallMobile {
        font-size: 1em;
      }*/

  }

@media(max-width:768px){
  .text_box{
    width: 100%;
    margin-left: 0 !important;
  }
  .condo-text p {
    width: 100% !important;
  }
  .adultCondosHeader {
    width: 100%;
    font-size: 2.8em;
  }
  .map_box {
    margin-left: 0 !important;
  }
  .adult-condo h1 {
    width: 100%;
  }
  img#mapper {
    width: 80% !important;
    padding-top: 15%;
  }

  .condo-text p {
    font-size: 1.5em;
  }
}

@media(max-width:600px){
  .FormInput{
    width: 75%;
    margin-left: 12.5%;
  }
}

@media(max-width:414px){
    .condo-text p {
    font-size: 1em;
  }
  .adultCondosHeader {
    font-size: 2em;
  }
}



