
#siteplan_container {
  padding-left: 10%;
}

.spPic-container {
 /* position: relative;
  z-index: -1;
  overflow:hidden;
  text-align: center;*/
  width: 80%;
  overflow:hidden;
  float: left;
  position: relative;
}
.spLegendContainer {
  width: 20%;
  float: left;
}
.spLegendPic {
  width: 100%;
  display: block;
  margin-top: -6%;
  margin-left: -8%;
}
#features1 {
  font-size: 4rem;
  color: #008dd3;
  font-family: GothamLight;
  font-weight: bolder;
}

.spbtn:hover {
color: #08c;
background: #e7e7e7;
text-shadow: 0 1px 0px rgba(255, 255, 255, .25);
border: 1px solid #08c;
-o-transition:.5s;
-ms-transition:.5s;
-moz-transition:.5s;
-webkit-transition:.5s;
text-decoration:none;
}
.spbtn {
  display: inline-block;
  padding: 10px 14px;
  margin-bottom: 25px;
  font-size: 18px;
  font-family: GothamBold;
  line-height: 20px;
  color: #f1f1f1;
  text-align: center;
  text-shadow: 0 1px 0px rgba(255, 255, 255, .125);
  vertical-align: middle;
  cursor: pointer;
  background-color: #08c;
  border: 1px solid #e7e7e7;
  border-radius: 6px;

}

.spPic {
  width: 100%;
  height: auto;
}


.spZoomBtnContainer {
  margin-top: 3rem;
  width: 100%;
  text-align: justify;
}
.spZoomBtnContainer:after {
  content:' ';
  display:inline-block;
  width: 100%;
  height: 0;
}
.spZoomBtn {
  cursor: pointer;
  width: 20%;
}
.spZoomBtn img {
  width: 100%;
  display: block;
}
.spZoomIn {
  float: left;
  margin-left:  25%;
}

.spZoomOut {
  float: right;
  margin-right: 25%;
  margin-left:  10%;
}

#site1 {

}


.spPicOverlayInfo div {

}

.mobileSiteplanRow {
  display: none;
}
/*Media Queries*/

@media(max-width:1024px){
  .row .spmapbtncontainer  {
    margin-top: -11rem;
    margin-right: 6rem;
  }
  #siteplan_container {
    padding-left: 10%;
  }

}

  @media(max-width:768px){
    .row .spmapbtncontainer {
      margin-right: 3rem;
      margin-top: -8rem;
  }
  #site1 {
    margin-left:0;
  }
  .spbtn {
    width: auto;
  }
  .spPic {
    margin-left: 0px !important;
  }

  .row .spmapbtncontainer {
    margin-right: 0rem;
    margin-top: -9rem;
  }
  .spPic-container {
    display: none;
  }
  .spLegendContainer {
    display: none;
  }
  #siteplan_container {
    padding-left: 15px;
  }
  .mobileSiteplanRow {
    display: block;
  }
  .mobileSiteplan {
    width: 100%;
  }
  .row.mobileSiteplanRow {
    padding-bottom: 8%;
  }
}

  @media(max-width:414px){
    .siteplan_container {
    }
    .row .spmapbtncontainer {
      display: none;
    }
    #siteplan_container {
      padding-left: 0rem;
    }
    h1#site1 {
      padding-left: 15px;
    }
    #features1 {
      display: inline-block;
      padding-left:7%;

    }
    .row.sitebtncontainer {
      display: inline-block;
    } 
    .spbtn {
      margin-bottom: 15px;
      margin-left: 15px;
    }
    .mobileSiteplanInner {
      padding-left: 0;  
      padding-right: 0;
    }
  }
