.featuresHeading {

	width: 100%;
}

.features_container {
	padding-left: 10%;
	padding-right: 10%;
}

#features1 {
  font-size: 4rem;
  color: #008dd3;
  font-family: GothamLight;
	font-weight: bold;
	margin-top: 5%;
}

.featbtn:hover {
color: #08c;
background: #e7e7e7;
text-shadow: 0 1px 0px rgba(255, 255, 255, .25);
border: 1px solid #08c;
-o-transition:.5s;
-ms-transition:.5s;
-moz-transition:.5s;
-webkit-transition:.5s;
text-decoration:none;
}
.featbtn {
display: inline-block;
padding: 10px 14px;
margin-bottom: 0;
font-size: 18px;
font-family: GothamBold;
line-height: 20px;
color: #f1f1f1;
text-align: center;
text-shadow: 0 1px 0px rgba(255, 255, 255, .125);
vertical-align: middle;
cursor: pointer;
background-color: #08c;
border: 1px solid #e7e7e7;
border-radius: 6px;
}

#featuresText {
  padding-top: 2rem;
  font-family: GothamBook;
  font-size: 18px;
  width: 100%;
}

.gal-container{
  padding: 0!important;
  width: 100% !important
}
.gal-item{
  overflow: hidden;
  padding: 15px;
}
.gal-item .box{
  height: 303px;
  overflow: hidden;
}

.bigBox {
    height: 635px !important;
}
.box img{
  height: 100%;
  width: 100%;
  object-fit:cover;
  -o-object-fit:cover;
}


.overlay {
  position: absolute;
  top: 50%;
  bottom: 0;
  right: 0;
  left: 0;
  width: 100%;
  height: 100%;
  text-align: center;
  color: white;
  -webkit-transition: all 0.5s ease-out;
  -o-transition: all 0.5s ease-out;
  transition: all 0.5s ease-out;
  font-family: GothamBook;
  font-size: 22px;
  font-size: 1.3vw;
}

.overlayMulti {
	top: 43%;
}

.img-overlay img {
  margin: 0;
  opacity: 0.6;
  display: block;

}

 .box {
  background-color: rgb(54, 171, 255);


}

.img-overlay:hover img {
  margin: 0;
  opacity: 1;
  display: block;
  -webkit-transition: all 0.5s ease-out;
  -o-transition: all 0.5s ease-out;
  transition: all 0.5s ease-out;
}
.img-overlay:hover .overlay {
  display: none;

}

.coNoPad {
	padding: 0;
}
@media screen and (min-width:415px) and (max-width:767px) {
  .col-xs-5 {
    width: 50%;
  }

}

@media(max-width:414px){
.features_container {
    padding-left: 0%;
    padding-right: 0%;
/*    height: 203px;
*/  }
  .gal-item {
    padding-left:0;
    padding-right: 0;
    padding: 0;
  }
  .bigBox {
    height: 403px !important;
  }
  .mobileRearrange {
    float: right;
  }
  .mobileRearrangeTwo {
    padding:2px 0 2px 0;
  }

  .col-xss-6 {
    width: 49.666667%;
    display: inline-block;
    float: left;
  }
  .col-xs-5 {
    width: 49.666667%;
  }
  .faucet {
    padding-bottom: 2px;
  }
  .featuresBottomSmall {
    padding-top: 2px;
  }
  .featuresBottomLarge {
    padding-top:2px;
  }
  .box {
    background-color: white;
  }
  .gal-item .box {
    height: 200px;
  }
  .overlayMulti {
    top: 76%;
    background-color: blue;
    font-size: 2.3vw;
    padding-top: 9px;
  }
  .overlay {
    top: 80%;
    background-color: rgba(66, 164, 215, 0.8);
    font-size: 2.3vw;
    padding-top: 9px;
  }
  .img-overlay img {
    opacity: 1;
  }
  .hideBreak {
 content: " ";
    display: none;
  }
  .hideFeatureCopy {
    display: none;
    content: " ";
    visibility: hidden;
  }
  .overlay.spicerack {
    padding-top: 15px;
  }
  .overlay.Valance {
    top:90%;
    padding-top: 15px;
  }
  .overlay.overlayMulti.countertops {
    padding-top: 11px;
  }
  #features1 {
    font-size: 23pt !important;
  }
  .mobileHide {
    display: none;
  }
  h1#features1 {
    display: inline;
  }
  .featbtn {
    width: 100%;
    margin-top: 20px;
  }
}
@media(max-width:375px){
    #features1 {
      font-size: 24px !important;
    }
  }

